@font-face {
  font-family: 'rc-crop';
  /* project id："178487" */
  src: url('//at.alicdn.com/t/font_b46n7aif4ci35wmi.eot');
  src: url('//at.alicdn.com/t/font_b46n7aif4ci35wmi.eot') format('embedded-opentype'), url('//at.alicdn.com/t/font_b46n7aif4ci35wmi.woff') format('woff'), url('//at.alicdn.com/t/font_b46n7aif4ci35wmi.ttf') format('truetype'), url('//at.alicdn.com/t/font_b46n7aif4ci35wmi.svg#rc-crop') format('svg');
}
.cropper-icon {
  display: inline-block;
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;
}
.cropper-icon:before {
  display: block;
  font-size: 14px;
  font-family: "rc-crop" !important;
}
.cropper-icon-picture:before {
  content: '\e602';
}
.cropper-icon-upload:before {
  content: '\e617';
}
.cropper-icon-delete:before {
  content: '\e656';
}
.rc-preview {
  position: relative;
}
.rc-preview-wrapper {
  padding: 8px;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  vertical-align: top;
  display: inline-block;
}
.rc-preview-mask {
  opacity: 0;
  cursor: pointer;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  font-size: 36px;
  color: #fff;
  text-align: center;
  transition: opacity 0.15s ease-in-out;
}
.rc-preview-mask:hover {
  opacity: 1;
}
.rc-preview-mask .anticon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -18px;
  margin-left: -18px;
}
.rc-cropper-wrapper {
  overflow: hidden;
  background-color: #f2f2f2;
  padding-right: 200px;
  position: relative;
}
.rc-thumbnail-preview {
  position: absolute;
  height: 100%;
  width: 200px;
  right: 0;
  top: 0;
  padding: 0 14px;
  background-color: white;
}
.rc-thumbnail-preview .size-1x,
.rc-thumbnail-preview .size-2x {
  text-align: center;
  margin: 20px 0;
}
.rc-thumbnail-preview canvas {
  image-rendering: optimizeQuality;
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
}
.rc-thumbnail-preview canvas.radius {
  border-radius: 50%;
}
.rc-crop-viewer-uploader {
  width: 100%;
  height: 100%;
  border: 1px dashed #d9d9d9;
  transition: border-color 0.3s ease;
  cursor: pointer;
  border-radius: 6px;
  text-align: center;
  position: relative;
}
.rc-crop-viewer-uploader .rc-upload-drag-icon .anticon {
  font-size: 80px;
  margin-top: -5px;
  color: #57c5f7;
}
.rc-crop-viewer-uploader .rc-upload-text {
  font-size: 14px;
}
.rc-crop-viewer-uploader:hover {
  border-color: #57c5f7;
}
.rc-cropper {
  padding-bottom: 34px;
  padding-top: 40px;
  width: 100%;
  height: 100%;
  position: relative;
}
.rc-thumbnail {
  width: 320px;
  height: 320px;
  margin: 0 auto;
  position: relative;
}
.rc-thumbnail img {
  position: absolute;
  top: 0;
}
.rc-thumbnail .thumbnail-window {
  width: 320px;
  height: 320px;
  box-shadow: 0 0 2px rgba(45, 183, 245, 0.5);
  overflow: hidden;
  position: relative;
}
.rc-background {
  position: absolute;
  opacity: .3;
  cursor: move;
}
.rc-scaller {
  display: inline-block;
  text-align: center;
  margin-right: 200px;
}
.rc-scaller button {
  appearance: none;
  background: none;
  border: none;
  cursor: pointer;
}
.rc-scaller button:disabled {
  opacity: .3;
}
.rc-scaller .anticon {
  margin: 10px 5px;
}
.rc-scaller .anticon.smaller {
  font-size: 12px;
}
.rc-scaller .anticon.larger {
  font-size: 16px;
}
.rc-scaller .rc-slider {
  width: 150px;
  display: inline-block;
  vertical-align: bottom;
  border-color: transparent;
  margin: 3px 5px;
}
.candrag-notice-wrapper {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 20px;
}
.candrag-notice-wrapper .candrag-notice {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid #fff;
  border-color: rgba(255, 255, 255, 0.8);
  border-radius: 3px;
  box-shadow: 0 0 2px rgba(255, 255, 255, 0.5);
  color: #fff;
  display: inline-block;
  font-weight: 700;
  padding: 6px 12px;
}
.rc-btn {
  display: inline-block;
  margin-bottom: 0;
  font-weight: 500;
  text-align: center;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  line-height: 1.5;
  padding: 4px 15px;
  font-size: 12px;
  border-radius: 6px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  position: relative;
  color: #666;
  background-color: #f7f7f7;
  border-color: #d9d9d9;
}
.rc-btn-primary {
  color: #fff;
  background-color: #2db7f5;
  border-color: #2db7f5;
}
.rc-btn-primary:hover {
  color: #fff;
  background-color: #57c5f7;
  border-color: #57c5f7;
}
.rc-btn-ghost {
  color: #666;
  background-color: transparent;
  border-color: #d9d9d9;
}
.rc-btn-ghost:hover {
  color: #57c5f7;
  background-color: transparent;
  border-color: #57c5f7;
}



